html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000;
  font-family: "Roboto", sans-serif;
}

a {
  outline: unset;
  text-decoration: unset;
  color: #000000;
}

button {
  background-color: unset;
  margin: 0;
  padding: 0;
  border: unset;
  outline: unset;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 36px;
}
@media only screen and (min-width: 900px) {
  h1 {
    font-size: 60px;
  }
}

h2 {
  font-size: 24px;
}
@media only screen and (min-width: 900px) {
  h2 {
    font-size: 48px;
  }
}

h3 {
  font-size: 16px;
}
@media only screen and (min-width: 900px) {
  h3 {
    font-size: 18px;
  }
}

p {
  font-size: 16px;
  line-height: 22px;
}
@media only screen and (min-width: 900px) {
  p {
    font-size: 18px;
    line-height: 24px;
  }
}

#main_nav {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: baseline;
  gap: 10vw;
  width: 100%;
  background-color: #6d888e;
  padding: 15px;
}
@media only screen and (min-width: 900px) {
  #main_nav {
    padding: 15px 25px;
    gap: 150px;
  }
}
#main_nav .nav_link {
  color: #9d9d9d;
  padding: 5px 10px;
}
#main_nav .nav_link.active {
  color: #ffffff;
}
#main_nav .nav_link.name_link {
  color: #ffffff;
  font-size: 24px;
  line-height: 28px;
}
#main_nav .nav_content {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
#main_nav #hide-show_menu {
  position: relative;
  display: inline-flex;
  width: 32px;
  height: 19px;
  cursor: pointer;
}
#main_nav #hide-show_menu .line {
  position: absolute;
  display: block;
  background-color: #ffffff;
  width: 32px;
  height: 3px;
  transition: all 0.2s ease-in-out;
}
#main_nav #hide-show_menu .line:nth-child(1) {
  top: 0px;
}
#main_nav #hide-show_menu .line:nth-child(2) {
  top: 8px;
}
#main_nav #hide-show_menu .line:nth-child(3) {
  top: 16px;
}
#main_nav #hide-show_menu.active .line:nth-child(1) {
  transform: rotate(45deg);
  top: 8px;
}
#main_nav #hide-show_menu.active .line:nth-child(2) {
  height: 0;
}
#main_nav #hide-show_menu.active .line:nth-child(3) {
  transform: rotate(-45deg);
  top: 8px;
}
#main_nav #hide-show_menu.active + .nav_conent-menu.menu-toggle {
  display: flex;
}
#main_nav .nav_conent-menu {
  display: flex;
  width: 100%;
}
#main_nav .nav_conent-menu.menu-toggle {
  display: none;
  position: absolute;
  top: calc(100% + 30px);
  right: -15px;
  flex-direction: column;
  align-items: center;
  background-color: #6d888e;
  box-shadow: 0 0 2px #9d9d9d;
  z-index: 9;
}
#main_nav .nav_conent-menu.menu-toggle .nav_conent-menu--user {
  margin: 0 auto;
}
#main_nav .nav_conent-menu--user {
  padding: 5px 0;
  margin: 0 0 0 auto;
}
#main_nav .nav_conent-menu--user .hide-show_userpanel {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  font-size: 0;
  border: 1px solid #ffffff;
  background-image: url("../../images/ico-user-white.svg");
  background-size: 19px 25px;
  background-repeat: no-repeat;
  background-position: center;
}
#main_nav .nav_conent-menu--user .hide-show_userpanel + .hide-show_userpanel-box {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  padding: 10px 25px;
  background-color: #ffffff;
  box-shadow: 0 0 2px #9d9d9d;
  z-index: 9;
}
@media only screen and (min-width: 760px) {
  #main_nav .nav_conent-menu--user .hide-show_userpanel + .hide-show_userpanel-box {
    top: calc(100% + 15px);
    right: -25px;
    left: unset;
  }
}
#main_nav .nav_conent-menu--user .hide-show_userpanel + .hide-show_userpanel-box > a {
  display: block;
  text-align: center;
  padding: 5px;
}
#main_nav .nav_conent-menu--user .hide-show_userpanel.active + .hide-show_userpanel-box {
  display: block;
}

body.about #main_nav,
body.about #main_nav .nav_conent-menu.menu-toggle {
  background-color: #e1eae4;
}
body.main #main_nav,
body.main #main_nav .nav_conent-menu.menu-toggle {
  background-color: #ffffff;
}
body.about #main_nav .nav_link, body.main #main_nav .nav_link {
  color: #9d9d9d;
}
body.about #main_nav .nav_link.active, body.main #main_nav .nav_link.active {
  color: #000000;
}
body.about #main_nav .nav_link.name_link, body.main #main_nav .nav_link.name_link {
  color: #000000;
}
body.about #main_nav #hide-show_menu .line, body.main #main_nav #hide-show_menu .line {
  background-color: #000000;
}
body.about #main_nav .nav_conent-menu--user .hide-show_userpanel, body.main #main_nav .nav_conent-menu--user .hide-show_userpanel {
  border: 1px solid #9d9d9d;
  background-image: url("../../images/ico-user.svg");
}

.bgr-img {
  position: relative;
  width: 100%;
  height: 583px;
}
.bgr-img img {
  width: 100%;
  height: inherit;
  object-fit: cover;
  object-position: center;
}
.bgr-img h1 {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  width: 100%;
  margin: 0;
}

.main_content-inner {
  padding: 50px 15px;
  box-sizing: border-box;
}
@media only screen and (min-width: 900px) {
  .main_content-inner {
    padding: 60px;
  }
}
.main_content-inner--header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
@media only screen and (min-width: 900px) {
  .main_content-inner .main_content-text {
    max-width: 620px;
  }
}
.main_content-inner .item_content {
  display: grid;
  gap: 35px;
  grid-template-columns: 1fr;
  margin: 0 0 50px;
}
@media only screen and (min-width: 900px) {
  .main_content-inner .item_content {
    margin: 100px 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
.main_content-inner .item_content > img {
  max-width: 100%;
  height: inherit;
}

.button-link {
  display: inline-block;
  height: fit-content;
  font-size: 14px;
  color: #ffffff;
  background-color: #6d888e;
  padding: 14px 30px;
  border-radius: 10px;
  box-sizing: border-box;
}

.main_content-collection {
  max-width: 1600px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin: 50px auto;
}
@media only screen and (min-width: 900px) {
  .main_content-collection {
    grid-template-columns: repeat(4, 1fr);
    gap: 34px;
  }
}

.main_content-collection--item .item-img {
  margin-bottom: 10px;
}
@media only screen and (min-width: 900px) {
  .main_content-collection--item .item-img {
    margin-bottom: 20px;
  }
}
.main_content-collection--item .item-img img {
  width: 100%;
  height: inherit;
  object-fit: cover;
  object-position: center;
}

#aboutpage .bgr-img,
#collectionpage .bgr-img,
#itempage .bgr-img {
  width: 100%;
  height: 240px;
  background: #e1eae4;
}
#aboutpage .bgr-img img,
#collectionpage .bgr-img img,
#itempage .bgr-img img {
  opacity: 0.2;
}

.navigation-bar {
  padding: 30px 15px;
  border-bottom: 1px solid #9d9d9d;
  color: #9d9d9d;
  font-size: 18px;
}
@media only screen and (min-width: 900px) {
  .navigation-bar {
    padding: 30px 60px;
  }
}
.navigation-bar a {
  color: #9d9d9d;
  font-size: 18px;
  text-decoration: underline;
}
.navigation-bar a:last-of-type {
  text-decoration: unset;
  pointer-events: none;
}